@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
    font-family: "Poppins", sans-serif;
    margin: 0;
    padding: 0;
}

/* Scroll */
body::-webkit-scrollbar { width: 0.4rem; }
body::-webkit-scrollbar-track { background: #d3d3d3; }
body::-webkit-scrollbar-thumb { background: #F47E1F; }

/* Colors */
.blue-bg { background: #0C2384; }
.orange-bg { background: #F47E1F; }
.white-bg { background: #FFFFFF; }

.blue-text { color: #0C2384; }
.orange-text { color: #F47E1F; }
.white-text { color: #FFFFFF; }

/* Flex */
.flex { display: flex; }
.column { flex-direction: column; }

/* Fonts */
h1 { font-size: 3rem; }
h2 { font-size: 3rem; }
h4 { font-size: 1.8rem; }
h5 { font-size: 1.4rem; }
h6 { font-size: 1.2rem; }
a { font-size: 1.7rem; text-decoration: none; }
p { font-size: 1.8rem; }

.fw-300 { font-weight: 300; }
.fw-400 { font-weight: 400; }
.fw-500 { font-weight: 500; }
.fw-600 { font-weight: 600; }

/* Margin */
.margin-0 { margin: 0; }

/* Transitions */
.transition-1 { transition: 0.5s; }

@media (max-width: 1185px) {

    h2, h4, p { line-height: 1.8rem !important; }
    h5, h6 { line-height: 1.5rem; }
    h2 { font-size: 1.8rem; }
    h4 { font-size: 1.5rem; }
    h5 { font-size: 1.3rem; }
    h6 { font-size: 1.1rem; }
    p { font-size: 1.2rem; }

}